.slideshow {
  position: relative;
  width: 100%;
  height: 415px;
  border-radius: 25px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;

  .slideshowImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .slideshowArrows {
    position: absolute;
    width: 100%;

    img {
      width: 46px;
      height: 79px;
      cursor: pointer;
    }

    .leftArrow {
      position: relative;
      left: 25px;
    }

    .rightArrow {
      position: absolute;
      right: 25px;
    }
  }

  .slideshowCounter {
    position: absolute;
    bottom: 15px;
    color: white;
    padding: 10px 0px;
    font-size: 18px;
    line-height: 25px;
  }
}

/*** MEDIA QUERIES ***/

/* (768PX AND LESS TABLET) */
@media (max-width: 768px) {
  .slideshow {
    height: 255px;
    border-radius: 10px;
    .slideshowArrows {
      img {
        width: 14px;
      }

      .leftArrow {
        left: 10px;
      }

      .rightArrow {
        right: 10px;
      }
    }
  }

  .slideshowCounter {
    display: none;
  }
}
